import axios from 'axios';
import { createStore } from 'vuex'
import { timeConverte } from '@/composition-api/useTimeConverter'
export default createStore({
  state: {
    originID: '',
    destinationID: '',
    trainDate: '',
    trainTime: '',
    trainData: [],
    storageData: JSON.parse(localStorage.getItem('data')) || [],
  },
  actions: {
    updateOriginID(context, e){
      const newID = e.target.value;
      //獲取選項的id
      context.commit('updateOriginID', newID);
    },
    updateDestinationID(context, e){
      const newID = e.target.value;
      //獲取選項的id
      context.commit('updateDestinationID', newID);
    },
    updateTrainDate(context, e){
      const newDate = e.target.value;
      //獲取選項的日期
      context.commit('updateTrainDate', newDate);
    },
    updateTrainTime(context, e){
      const newTime = e.target.value;
      //獲取選項的日期
      context.commit('updateTrainTime', newTime);
    },
    handTrainData(context){
      if (context.state.originID.length === 0 
      || context.state.destinationID.length === 0 
      || context.state.trainDate.length === 0 
      || context.state.trainTime.length === 0){
        let str = '';
        if(context.state.originID.length === 0){str += '啟程站 ';}
        if(context.state.destinationID.length === 0){str += '到達站 ';}
        if(context.state.trainDate.length === 0){str += '去程日期 ';}
        if(context.state.trainTime.length === 0){str += '去程時間 ';}
        alert(str + '不得為空');
        //判斷是否有空格      
      }else if (context.state.originID == context.state.destinationID){
        alert('啟程站跟到達站不得相同');
        //判斷啟程跟到達是否相同
      }else{
        axios
        .get(`https://ptx.transportdata.tw/MOTC/v2/Rail/THSR/DailyTimetable/OD/${context.state.originID}/to/${context.state.destinationID}/${context.state.trainDate}?$format=JSON`)
        .then((res)=>{
          context.commit('handTrainData', res.data);
          //撈出當天起迄站的資料
        })
      };
    },
    handStorageData(context, index){
      context.commit('handStorageData', index);
    },
    handDeleteStorage(context, index){
      context.commit('handDeleteStorage', index);
    },
  },
  mutations: {
    updateOriginID(state, newID){
      state.originID = newID;
    },
    updateDestinationID(state, newID){
      state.destinationID = newID;
    },
    updateTrainDate(state, newDate){
      state.trainDate = newDate;
    },
    updateTrainTime(state, newTime){
      state.trainTime = newTime;
    },
    handTrainData(state, data){
      state.trainData = data.filter((el) => {
        return timeConverte(el.OriginStopTime.ArrivalTime) >= timeConverte(state.trainTime)
        //篩選到達時間晚於選取時間的資料
      });
    },
    handStorageData(state, index){
      state.storageData.push(state.trainData[index]);
      localStorage.setItem('data', JSON.stringify(state.storageData));
      //先取得localStorage 先推入storageData在丟入localStorage
    }, 
    handDeleteStorage(state, index){
      state.storageData.splice(state.trainData[index], 1);
      localStorage.setItem('data', JSON.stringify(state.storageData));
    },
  },

  getters: {
    trainDataSelected(state){
      return state.trainData;
    },
    storageData(state){
      return  state.storageData;
    },
  }
})
