<script>
import { useStore }from 'vuex';
export default {
    setup(){
        const store = useStore();

        const updateTrainTime = (e) => {
            store.dispatch('updateTrainTime', e);
        };

        return{ updateTrainTime };
    }

}
</script>

<template>
    <label class="block">
        <span class="text-gray-700 text-sm">去程時間</span>
        <input 
        @change="updateTrainTime"
        type="time" 
        class="h-12 mt-1 block w-full rounded-md bg-gray-100 border-transparent focus:border-orange-600 hover:bg-orange-100 duration-400 focus:bg-white focus:ring-0">
    </label>
</template>
