<script>
import { computed } from 'vue';
import { useStore } from 'vuex';
import { timeConverte } from '@/composition-api/useTimeConverter'

export default {
    setup(){
        const store = useStore();

        const storageDataArr = computed(() => {
            return store.getters.storageData;
         });

        const compareTime = function(OriginStopTime, DestinationTime){
            const date = new Date();
            date.setHours(0);
            date.setMinutes(timeConverte(DestinationTime) - timeConverte(OriginStopTime));
            return `${date.getHours()}時${date.getMinutes()}分`;
            //運用date 相減兩個時間
        };

        const handDeleteStorage = (index) => {
            store.dispatch('handDeleteStorage', index);
        };

        return{ storageDataArr, compareTime, handDeleteStorage };
    }

}
</script>

<template>
    <table class="table-auto w-full text-center ">
        <thead>
            <tr class="border-b tracking-wider bg-gray-600 text-gray-100">
            <th class="w-1/4 md:w-1/5 py-2 font-normal">車次</th>
            <th class="w-1/4 md:w-1/5 font-normal">出發時間</th>
            <th class="w-1/4 md:w-1/5 font-normal">到達時間</th>
            <th class="md:w-1/5 font-normal hidden md:table-cell">行車時間</th>
            <th class="w-1/4 md:w-1/5 font-normal">刪除暫存</th>
            </tr>
        </thead>
        <tbody class="divide-y bg-gray-50 max-h-96" v-if="!storageDataArr.length == 0">
            <tr
            v-for="item in storageDataArr"
            :key = "item.DailyTrainInfo.TrainNo"
            class="hover:bg-orange-100 duration-400 align-middle ">
                <td>{{item.DailyTrainInfo.TrainNo}}</td>
                <td class="text-2xl py-3 md:text-3xl">
                    <span class="text-sm block">{{item.OriginStopTime.StationName.Zh_tw}}</span>
                    {{item.OriginStopTime.ArrivalTime}}
                </td>
                <td class="text-2xl py-3 md:text-3xl ">
                    <span class="text-sm block">{{item.DestinationStopTime.StationName.Zh_tw}}</span>
                    {{item.DestinationStopTime.ArrivalTime}}
                </td>
                <td class="hidden md:table-cell">{{compareTime(item.OriginStopTime.ArrivalTime, item.DestinationStopTime.ArrivalTime)}}</td>
                <td>
                    <div @click="handDeleteStorage(index)">
                        <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" version="1.1" class="m-auto fill-current  text-red-600 hover:text-red-700 duration-400 cursor-pointer" id="Capa_1" x="0px" y="0px" width="25pt" height="25pt" viewBox="0 0 342.514 342.514" style="enable-background:new 0 0 342.514 342.514;" xml:space="preserve"><g>
	                    <path d="M171.254,0C76.819,0,0,76.825,0,171.26c0,94.434,76.813,171.254,171.254,171.254c94.423,0,171.26-76.82,171.26-171.254   C342.514,76.825,265.677,0,171.254,0z M235.652,185.056H106.855c-7.623,0-13.799-6.185-13.799-13.796   c0-7.617,6.176-13.799,13.799-13.799h128.797c7.614,0,13.799,6.182,13.799,13.799C249.451,178.871,243.267,185.056,235.652,185.056   z"/></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g><g></g></svg>
                    </div>
                </td>
            </tr>
        </tbody>
    </table>

