<script>
import { computed } from 'vue';
import { useStore } from 'vuex';
import { timeConverte } from '@/composition-api/useTimeConverter'

export default {
    setup(){
        const store = useStore();
        const timetableArr = computed(() => {
            return store.getters.trainDataSelected;
         });

        const compareTime = function(OriginStopTime, DestinationTime){
            const date = new Date();
            date.setHours(0);
            date.setMinutes(timeConverte(DestinationTime) - timeConverte(OriginStopTime));
            return `${date.getHours()}時${date.getMinutes()}分`;
            //運用date 相減兩個時間
        };
        
        const handStorageData = (index) => {
            store.dispatch('handStorageData', index);
        };

        return{ timetableArr, compareTime, handStorageData };
    }

}
</script>

<template>
    <table class="table-auto w-full text-center ">
    <thead>
        <tr class="w-1/4 md:w-1/5 border-b tracking-wider bg-gray-600 text-gray-100">
        <th class="w-1/4 md:w-1/5 py-2 font-normal">車次</th>
        <th class="w-1/4 md:w-1/5 font-normal">出發時間</th>
        <th class="w-1/4 md:w-1/5 font-normal">到達時間</th>
        <th class="md:w-1/5 font-normal hidden md:table-cell">行車時間</th>
        <th class="w-1/4 md:w-1/5 font-normal">加入暫存</th>
        </tr>
    </thead>
    <tbody class="divide-y bg-gray-50 max-h-96" v-if="!timetableArr.length==0">
        <tr
         v-for="(item, index) in timetableArr.slice(0, 5)"
         :key = "item.DailyTrainInfo.TrainNo"
        class="hover:bg-orange-100 duration-400 align-middle ">
            <td>{{item.DailyTrainInfo.TrainNo}}</td>
            <td class="text-2xl py-3 md:text-3xl">{{item.OriginStopTime.ArrivalTime}}</td>
            <td class="text-2xl py-3 md:text-3xl ">{{item.DestinationStopTime.ArrivalTime}}</td>
            <td class="hidden md:table-cell">{{compareTime(item.OriginStopTime.ArrivalTime, item.DestinationStopTime.ArrivalTime)}}</td>
            <td>
                <div @click="handStorageData(index)">
                    <svg xmlns="http://www.w3.org/2000/svg" height="25pt" viewBox="0 0 512 512" width="25pt" class="m-auto fill-current text-orange-600 hover:text-orange-900 duration-400 cursor-pointer">
                    <path d="m256 0c-141.164062 0-256 114.835938-256 256s114.835938 256 256 256 256-114.835938 256-256-114.835938-256-256-256zm112 277.332031h-90.667969v90.667969c0 11.777344-9.554687 21.332031-21.332031 21.332031s-21.332031-9.554687-21.332031-21.332031v-90.667969h-90.667969c-11.777344 0-21.332031-9.554687-21.332031-21.332031s9.554687-21.332031 21.332031-21.332031h90.667969v-90.667969c0-11.777344 9.554687-21.332031 21.332031-21.332031s21.332031 9.554687 21.332031 21.332031v90.667969h90.667969c11.777344 0 21.332031 9.554687 21.332031 21.332031s-9.554687 21.332031-21.332031 21.332031zm0 0"/></svg>
                </div>
            </td>
        </tr>
    </tbody>
    </table>

    <p 
    class="text-2xl text-center py-6 "
    v-if="timetableArr.length==0">目前沒有資料
    </p>

</template>