<script>
import { useStore }from 'vuex';
export default {
    setup(){
        const store = useStore();
        
        const updateTrainDate = (e) => {
            store.dispatch('updateTrainDate', e);
        };

        return{ updateTrainDate };
    }

}
</script>

<template>
    <label class="block">
        <span class="text-gray-700 text-sm">去程日期</span>
        <input 
        @change="updateTrainDate"
        type="date"
        class="h-12 mt-1 block w-full rounded-md bg-gray-100 border-transparent hover:bg-orange-100 duration-400 focus:border-orange-600 focus:bg-white focus:ring-0">
    </label>
</template>
